import Vue from "vue";
//Dng ny  import vue-router
import Router from "vue-router";
import CompManagement from "../components/CompManagement";
import CompNhomNganh from "../components/CompNhomNganh";
import CompNganh from "../components/CompNganh";
import CompLogin from "../components/CompLogin";
import CompRegistration from "../components/CompRegister";
import CompSearch from "../components/CompSearch";

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/", ///path ca route
      name: "CompSearch", // tn route
      component: CompSearch, // component route s dng
    },
    // bao gm danh sch route
    {
      path: "/management", ///path ca route
      name: "CompManagement", // tn route
      component: CompManagement, // component route s dng
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/management/group", ///path ca route
      name: "CompNhomNganh", // tn route
      component: CompNhomNganh, // component route s dng
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/management/industry", ///path ca route
      name: "CompNganh", // tn route
      component: CompNganh, // component route s dng
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "/login", ///path ca route
      name: "CompLogin", // tn route
      component: CompLogin, // component route s dng
    },
    {
      path: "/registration", ///path ca route
      name: "CompRegistration", // tn route
      component: CompRegistration, // component route s dng
    },
  ],
  mode: "history",
});
router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    // console.log(Vue.$cookies.get("cookToken"));
    if (Vue.$cookies.get("cookToken") == null) {
      next({
        path: "/login",
        params: { nextUrl: to.fullPath },
      });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
